.rnd-button {
	padding: 10px 15px;

	background-color: #fafafa;

	border: 1px solid #d3d4d4;
	border-radius: 20px;

	cursor: pointer;

	&:hover {
		background-color: #cacaca;
	}
}

.btn__click {
	&:active {
		transition: 0.1s ease;
		transform: scale(0.95);
	}
}

input[type="range"]::-webkit-slider-thumb {
	-webkit-appearance: none;

	position: relative;

	height: 20px;
	width: 20px;

	background: #fff;

	margin-top: -10px;

	border-radius: 50%;

	transition: all 0.3s ease-in-out;

	cursor: pointer;

	&:hover {
		height: 25px;
		width: 25px;

		margin-top: -10px;

		box-shadow: 0 0 2px 5px rgba(255, 255, 255, 0.603);
	}
}
input[type="range"]::-moz-range-thumb {
	-webkit-appearance: none;

	position: relative;

	height: 20px;
	width: 20px;

	margin-top: -10px;

	background: #fff;

	border-radius: 50%;

	transition: all 0.3s ease-in-out;

	cursor: pointer;

	&:hover {
		height: 25px;
		width: 25px;

		margin-top: -10px;

		box-shadow: 0 0 2px 5px rgba(255, 255, 255, 0.603);
	}
}

/* All the same stuff for IE */
input[type="range"]::-ms-thumb {
	-webkit-appearance: none;

	position: relative;

	height: 20px;
	width: 20px;

	margin-top: -10px;

	background: #fff;

	border-radius: 50%;

	transition: all 0.3s ease-in-out;

	cursor: pointer;

	&:hover {
		height: 25px;
		width: 25px;

		margin-top: -10px;

		box-shadow: 0 0 2px 5px rgba(255, 255, 255, 0.603);
	}
}
.range-field {
	& label {
		font-size: 20px;
		color: $darkgreen;
	}
}
//Slider track
input[type="range"]::-webkit-slider-runnable-track {
	background: #000;
}

input[type="range"]:focus::-webkit-slider-runnable-track {
	background: #000;
}

input[type="range"]::-moz-range-track {
	background: #000;
}

input[type="range"]::-ms-track {
	background: #000;
}
input[type="range"]::-ms-fill-lower {
	background: #000;
}
input[type="range"]:focus::-ms-fill-lower {
	background: #000;
}
input[type="range"]::-ms-fill-upper {
	background: #000;
}
input[type="range"]:focus::-ms-fill-upper {
	background: #000;
}
